import React from 'react';
import Card from './Card';

const CardList = ({ biaseslist }) => {
    if (false) {
        throw new Error('No');
    }
    return (
        <div>
            {biaseslist.map((user, i) => {
                return <Card key={i} id={biaseslist[i].id} title={biaseslist[i].title} description={biaseslist[i].description}/>
            })}
        </div>
    );
}

export default CardList;


