var biaseslist=[{"id":0,"title":"Ambiguity effect","description":"The tendency to avoid options for which missing information makes the probability seem 'unknown'."},{"id":1,"title":"Anchoring or focalism","description":"The tendency to rely too heavily, or 'anchor', on one trait or piece of information when making decisions (usually the first piece of information acquired on that subject)"},{"id":2,"title":"Anthropocentric thinking","description":"The tendency to use human analogies as a basis for reasoning about other, less familiar, biological phenomena."},{"id":3,"title":"Anthropomorphism or personification","description":"The tendency to characterize animals, objects, and abstract concepts as possessing human-like traits, emotions, and intentions."},{"id":4,"title":"Attentional bias","description":"The tendency of our perception to be affected by our recurring thoughts."},{"id":5,"title":"Automation bias","description":"The tendency to depend excessively on automated systems which can lead to erroneous automated information overriding correct decisions."},{"id":6,"title":"Availability heuristic","description":"A self-reinforcing process in which a collective belief gains more and more plausibility through its increasing repetition in public discourse (or 'repeat something long enough and it will become true')."},{"id":7,"title":"Backfire effect","description":"The reaction to disconfirming evidence by strengthening one's previous beliefs. cf. Continued influence effect."},{"id":8,"title":"Bandwagon effect","description":"The tendency to do (or believe) things because many other people do (or believe) the same. Related to 'groupthink' and herd behavior."},{"id":9,"title":"Base rate fallacy or Base rate neglect","description":"The tendency to ignore base rate information (generic, general information) and focus on specific information (information only pertaining to a certain case)."},{"id":10,"title":"Belief bias","description":"An effect where someone's evaluation of the logical strength of an argument is biased by the believability of the conclusion."},{"id":11,"title":"Ben Franklin effect","description":"A person who has performed a favor for someone is more likely to do another favor for that person than they would be if they had received a favor from that person."},{"id":12,"title":"Berkson's paradox","description":"The tendency to misinterpret statistical experiments involving conditional probabilities."},{"id":13,"title":"Bias blind spot","description":"The tendency to see oneself as less biased than other people, or to be able to identify more cognitive biases in others than in oneself."},{"id":14,"title":"Cheerleader effect","description":"The tendency for people to appear more attractive in a group than in isolation."},{"id":15,"title":"Choice-supportive bias","description":"The tendency to remember one's choices as better than they actually were."},{"id":16,"title":"Clustering illusion","description":"The tendency to overestimate the importance of small runs, streaks, or clusters in large samples of random data (that is, seeing phantom patterns)."},{"id":17,"title":"Confirmation bias","description":"The tendency to search for, interpret, focus on and remember information in a way that confirms one's preconceptions."},{"id":18,"title":"Congruence bias","description":"The tendency to test hypotheses exclusively through direct testing, instead of testing possible alternative hypotheses."},{"id":19,"title":"Conjunction fallacy","description":"The tendency to assume that specific conditions are more probable than general ones."},{"id":20,"title":"Conservatism (belief revision)","description":"The tendency to revise one's belief insufficiently when presented with new evidence."},{"id":21,"title":"Continued influence effect","description":"The tendency to believe previously learned misinformation even after it has been corrected. Misinformation can still influence inferences one generates after a correction has occurred. cf. Backfire effect"},{"id":22,"title":"Contrast effect","description":"The enhancement or reduction of a certain stimulus' perception when compared with a recently observed, contrasting object."},{"id":23,"title":"Courtesy bias","description":"The tendency to give an opinion that is more socially correct than one's true opinion, so as to avoid offending anyone."},{"id":24,"title":"Curse of knowledge","description":"When better-informed people find it extremely difficult to think about problems from the perspective of lesser-informed people."},{"id":25,"title":"Declinism","description":"The predisposition to view the past favourably (rosy retrospection) and future negatively."},{"id":26,"title":"Decoy effect","description":"Preferences for either option A or B change in favor of option B when option C is presented, which is completely dominated by option B (inferior in all respects) and partially dominated by option A."},{"id":27,"title":"Default effect","description":"When given a choice between several options, the tendency to favor the default one."},{"id":28,"title":"Denomination effect","description":"The tendency to spend more money when it is denominated in small amounts (e.g., coins) rather than large amounts (e.g., bills)."},{"id":29,"title":"Disposition effect","description":"The tendency to sell an asset that has accumulated in value and resist selling an asset that has declined in value."},{"id":30,"title":"Distinction bias","description":"The tendency to view two options as more dissimilar when evaluating them simultaneously than when evaluating them separately."},{"id":31,"title":"Dunning–Kruger effect","description":"The tendency for unskilled individuals to overestimate their own ability and the tendency for experts to underestimate their own ability."},{"id":32,"title":"Duration neglect","description":"The neglect of the duration of an episode in determining its value."},{"id":33,"title":"Empathy gap","description":"The tendency to underestimate the influence or strength of feelings, in either oneself or others."},{"id":34,"title":"Endowment effect","description":"The tendency for people to demand much more to give up an object than they would be willing to pay to acquire it."},{"id":35,"title":"Exaggerated expectation","description":"Based on the estimates, real-world evidence turns out to be less extreme than our expectations (conditionally inverse of the conservatism bias)."},{"id":36,"title":"Experimenter's or expectation bias","description":"The tendency for experimenters to believe, certify, and publish data that agree with their expectations for the outcome of an experiment, and to disbelieve, discard, or downgrade the corresponding weightings for data that appear to conflict with those expectations."},{"id":37,"title":"Focusing effect","description":"The tendency to place too much importance on one aspect of an event."},{"id":38,"title":"Forer effect or Barnum effect","description":"The observation that individuals will give high accuracy ratings to descriptions of their personality that supposedly are tailored specifically for them, but are in fact vague and general enough to apply to a wide range of people. This effect can provide a partial explanation for the widespread acceptance of some beliefs and practices, such as astrology, fortune telling, graphology, and some types of personality tests."},{"id":39,"title":"Framing effect","description":"Drawing different conclusions from the same information, depending on how that information is presented"},{"id":40,"title":"Frequency illusion","description":"The illusion in which a word, a name, or other thing that has recently come to one's attention suddenly seems to appear with improbable frequency shortly afterwards (not to be confused with the recency illusion or selection bias). This illusion may explain some examples of the Baader-Meinhof phenomenon, when someone repeatedly notices a newly learned word or phrase shortly after learning it."},{"id":41,"title":"Functional fixedness","description":"Limits a person to using an object only in the way it is traditionally used."},{"id":42,"title":"Gambler's fallacy","description":"The tendency to think that future probabilities are altered by past events, when in reality they are unchanged. The fallacy arises from an erroneous conceptualization of the law of large numbers. For example, 'I've flipped heads with this coin five times consecutively, so the chance of tails coming out on the sixth flip is much greater than heads'."},{"id":43,"title":"Hard–easy effect","description":"Based on a specific level of task difficulty, the confidence in judgments is too conservative and not extreme enough."},{"id":44,"title":"Hindsight bias","description":"Sometimes called the 'I-knew-it-all-along' effect, the tendency to see past events as being predictable at the time those events happened."},{"id":45,"title":"Hostile attribution bias","description":"The 'hostile attribution bias' is the tendency to interpret others' behaviors as having hostile intent, even when the behavior is ambiguous or benign."},{"id":46,"title":"Hot-hand fallacy","description":"The 'hot-hand fallacy' (also known as the 'hot hand phenomenon' or 'hot hand') is the fallacious belief that a person who has experienced success with a random event has a greater chance of further success in additional attempts."},{"id":47,"title":"Hyperbolic discounting","description":"Discounting is the tendency for people to have a stronger preference for more immediate payoffs relative to later payoffs. Hyperbolic discounting leads to choices that are inconsistent over time – people make choices today that their future selves would prefer not to have made, despite using the same reasoning. Also known as current moment bias, present-bias, and related to Dynamic inconsistency."},{"id":48,"title":"Identifiable victim effect","description":"The tendency to respond more strongly to a single identified person at risk than to a large group of people at risk."},{"id":49,"title":"IKEA effect","description":"The tendency for people to place a disproportionately high value on objects that they partially assembled themselves, such as furniture from IKEA, regardless of the quality of the end result."},{"id":50,"title":"Illusion of control","description":"The tendency to overestimate one's degree of influence over other external events."},{"id":51,"title":"Illusion of validity","description":"Belief that our judgments are accurate, especially when available information is consistent or inter-correlated."},{"id":52,"title":"Illusory correlation","description":"Inaccurately perceiving a relationship between two unrelated events."},{"id":53,"title":"Illusory truth effect","description":"A tendency to believe that a statement is true if it is easier to process, or if it has been stated multiple times, regardless of its actual veracity. These are specific cases of truthiness."},{"id":54,"title":"Impact bias","description":"The tendency to overestimate the length or the intensity of the impact of future feeling states."},{"id":55,"title":"Information bias","description":"The tendency to seek information even when it cannot affect action."},{"id":56,"title":"Insensitivity to sample size","description":"The tendency to under-expect variation in small samples."},{"id":57,"title":"Irrational escalation","description":"The phenomenon where people justify increased investment in a decision, based on the cumulative prior investment, despite new evidence suggesting that the decision was probably wrong. Also known as the sunk cost fallacy."},{"id":58,"title":"Law of the instrument","description":"An over-reliance on a familiar tool or methods, ignoring or under-valuing alternative approaches. 'If all you have is a hammer, everything looks like a nail.'"},{"id":59,"title":"Less-is-better effect","description":"The tendency to prefer a smaller set to a larger set judged separately, but not jointly."},{"id":60,"title":"Look-elsewhere effect","description":"An apparently statistically significant observation may have actually arisen by chance because of the size of the parameter space to be searched."},{"id":61,"title":"Loss aversion","description":"The disutility of giving up an object is greater than the utility associated with acquiring it. (see also Sunk cost effects and endowment effect)."},{"id":62,"title":"Mere exposure effect","description":"The tendency to express undue liking for things merely because of familiarity with them."},{"id":63,"title":"Money illusion","description":"The tendency to concentrate on the nominal value (face value) of money rather than its value in terms of purchasing power."},{"id":64,"title":"Moral credential effect","description":"The tendency of a track record of non-prejudice to increase subsequent prejudice."},{"id":65,"title":"Negativity bias or Negativity effect","description":"Psychological phenomenon by which humans have a greater recall of unpleasant memories compared with positive memories. (see also actor-observer bias, group attribution error, positivity effect, and negativity effect)."},{"id":66,"title":"Neglect of probability","description":"The tendency to completely disregard probability when making a decision under uncertainty."},{"id":67,"title":"Normalcy bias","description":"The refusal to plan for, or react to, a disaster which has never happened before."},{"id":68,"title":"Not invented here","description":"Aversion to contact with or use of products, research, standards, or knowledge developed outside a group. Related to IKEA effect."},{"id":69,"title":"Observer-expectancy effect","description":"When a researcher expects a given result and therefore unconsciously manipulates an experiment or misinterprets data in order to find it (see also subject-expectancy effect)."},{"id":70,"title":"Omission bias","description":"The tendency to be over-optimistic, overestimating favorable and pleasing outcomes (see also wishful thinking, valence effect, positive outcome bias)."},{"id":71,"title":"Ostrich effect","description":"Ignoring an obvious (negative) situation."},{"id":72,"title":"Outcome bias","description":"The tendency to judge a decision by its eventual outcome instead of based on the quality of the decision at the time it was made."},{"id":73,"title":"Overconfidence effect","description":"Excessive confidence in one's own answers to questions. For example, for certain types of questions, answers that people rate as '99% certain' turn out to be wrong 40% of the time."},{"id":74,"title":"Pareidolia","description":"A vague and random stimulus (often an image or sound) is perceived as significant, e.g., seeing images of animals or faces in clouds, the man in the moon, and hearing non-existent hidden messages on records played in reverse."},{"id":75,"title":"Pessimism bias","description":"The tendency for some people, especially those suffering from depression, to overestimate the likelihood of negative things happening to them."},{"id":76,"title":"Planning fallacy","description":"The tendency to underestimate task-completion times."},{"id":77,"title":"Post-purchase rationalization","description":"The tendency to persuade oneself through rational argument that a purchase was good value."},{"id":78,"title":"Pro-innovation bias","description":"The tendency to have an excessive optimism towards an invention or innovation's usefulness throughout society, while often failing to identify its limitations and weaknesses."},{"id":79,"title":"Projection bias","description":"The tendency to overestimate how much our future selves share one's current preferences, thoughts and values, thus leading to sub-optimal choices."},{"id":80,"title":"Pseudocertainty effect","description":"The tendency to make risk-averse choices if the expected outcome is positive, but make risk-seeking choices to avoid negative outcomes."},{"id":81,"title":"Reactance","description":"The urge to do the opposite of what someone wants you to do out of a need to resist a perceived attempt to constrain your freedom of choice (see also Reverse psychology)."},{"id":82,"title":"Reactive devaluation","description":"Devaluing proposals only because they purportedly originated with an adversary."},{"id":83,"title":"Recency illusion","description":"The illusion that a word or language usage is a recent innovation when it is in fact long-established (see also frequency illusion)."},{"id":84,"title":"Regressive bias","description":"A certain state of mind wherein high values and high likelihoods are overestimated while low values and low likelihoods are underestimated."},{"id":85,"title":"Restraint bias","description":"The tendency to overestimate one's ability to show restraint in the face of temptation."},{"id":86,"title":"Rhyme as reason effect","description":"Rhyming statements are perceived as more truthful. A famous example being used in the O.J Simpson trial with the defense's use of the phrase 'If the gloves don't fit, then you must acquit.'"},{"id":87,"title":"Risk compensation / Peltzman effect","description":"The tendency to take greater risks when perceived safety increases."},{"id":88,"title":"Selective perception","description":"The tendency for expectations to affect perception."},{"id":89,"title":"Semmelweis reflex","description":"The tendency to reject new evidence that contradicts a paradigm."},{"id":90,"title":"Sexual overperception bias / sexual underperception bias","description":"The tendency to over-/underestimate sexual interest of another person in oneself."},{"id":91,"title":"Social comparison bias","description":"The tendency, when making decisions, to favour potential candidates who don't compete with one's own particular strengths."},{"id":92,"title":"Social desirability bias","description":"The tendency to over-report socially desirable characteristics or behaviours in oneself and under-report socially undesirable characteristics or behaviours."},{"id":93,"title":"Status quo bias","description":"The tendency to like things to stay relatively the same (see also loss aversion, endowment effect, and system justification)."},{"id":94,"title":"Stereotyping","description":"Expecting a member of a group to have certain characteristics without having actual information about that individual."},{"id":95,"title":"Subadditivity effect","description":"The tendency to judge probability of the whole to be less than the probabilities of the parts."},{"id":96,"title":"Subjective validation","description":"Perception that something is true if a subject's belief demands it to be true. Also assigns perceived connections between coincidences."},{"id":97,"title":"Surrogation","description":"Losing sight of the strategic construct that a measure is intended to represent, and subsequently acting as though the measure is the construct of interest."},{"id":98,"title":"Survivorship bias","description":"Concentrating on the people or things that 'survived' some process and inadvertently overlooking those that didn't because of their lack of visibility."},{"id":99,"title":"Time-saving bias","description":"Underestimations of the time that could be saved (or lost) when increasing (or decreasing) from a relatively low speed and overestimations of the time that could be saved (or lost) when increasing (or decreasing) from a relatively high speed."},{"id":100,"title":"Third-person effect","description":"Belief that mass communicated media messages have a greater effect on others than on themselves."},{"id":101,"title":"Triviality / Parkinson's Law of","description":"The tendency to give disproportionate weight to trivial issues. Also known as 'bikeshedding', this bias explains why an organization may avoid specialized or complex subjects, such as the design of a nuclear reactor, and instead focus on something easy to grasp or rewarding to the average participant, such as the design of an adjacent bike shed."},{"id":102,"title":"Unit bias","description":"The tendency to want to finish a given unit of a task or an item. Strong effects on the consumption of food in particular."},{"id":103,"title":"Weber–Fechner law","description":"Difficulty in comparing small differences in large quantities."},{"id":104,"title":"Well travelled road effect","description":"Underestimation of the duration taken to traverse oft-traveled routes and overestimation of the duration taken to traverse less familiar routes."},{"id":105,"title":"'Women are wonderful' effect","description":"A tendency to associate more positive attributes with women than with men."},{"id":106,"title":"Zero-risk bias","description":"Preference for reducing a small risk to zero over a greater reduction in a larger risk."},{"id":107,"title":"Zero-sum bias","description":"A bias whereby a situation is incorrectly perceived to be like a zero-sum game (i.e., one person gains at the expense of another)."},{"id":108,"title":"Actor-observer bias","description":"The tendency for explanations of other individuals' behaviors to overemphasize the influence of their personality and underemphasize the influence of their situation (see also Fundamental attribution error), and for explanations of one's own behaviors to do the opposite (that is, to overemphasize the influence of our situation and underemphasize the influence of our own personality)."},{"id":109,"title":"Authority bias","description":"The tendency to attribute greater accuracy to the opinion of an authority figure (unrelated to its content) and be more influenced by that opinion."},{"id":110,"title":"Defensive attribution hypothesis","description":"Attributing more blame to a harm-doer as the outcome becomes more severe or as personal or situational similarity to the victim increases."},{"id":111,"title":"Egocentric bias","description":"Occurs when people claim more responsibility for themselves for the results of a joint action than an outside observer would credit them with."},{"id":112,"title":"Extrinsic incentives bias","description":"An exception to the fundamental attribution error, when people view others as having (situational) extrinsic motivations and (dispositional) intrinsic motivations for oneself"},{"id":113,"title":"False consensus effect","description":"The tendency for people to overestimate the degree to which others agree with them."},{"id":114,"title":"Forer effect (aka Barnum effect)","description":"The tendency to give high accuracy ratings to descriptions of their personality that supposedly are tailored specifically for them, but are in fact vague and general enough to apply to a wide range of people. For example, horoscopes."},{"id":115,"title":"Fundamental attribution error","description":"The biased belief that the characteristics of an individual group member are reflective of the group as a whole or the tendency to assume that group decision outcomes reflect the preferences of group members, even when information is available that clearly suggests otherwise."},{"id":116,"title":"Halo effect","description":"The tendency for a person's positive or negative traits to 'spill over' from one personality area to another in others' perceptions of them (see also physical attractiveness stereotype)."},{"id":117,"title":"Illusion of asymmetric insight","description":"People perceive their knowledge of their peers to surpass their peers' knowledge of them."},{"id":118,"title":"Illusion of external agency","description":"When people view self-generated preferences as instead being caused by insightful, effective and benevolent agents."},{"id":119,"title":"Illusion of transparency","description":"People overestimate others' ability to know them, and they also overestimate their ability to know others."},{"id":120,"title":"Illusory superiority","description":"Overestimating one's desirable qualities, and underestimating undesirable qualities, relative to other people. (Also known as 'Lake Wobegon effect', 'better-than-average effect', or 'superiority bias'.)"},{"id":121,"title":"Ingroup bias","description":"The tendency for people to give preferential treatment to others they perceive to be members of their own groups."},{"id":122,"title":"Just-world hypothesis","description":"The tendency for people to want to believe that the world is fundamentally just, causing them to rationalize an otherwise inexplicable injustice as deserved by the victim(s)."},{"id":123,"title":"Moral luck","description":"The tendency for people to ascribe greater or lesser moral standing based on the outcome of an event."},{"id":124,"title":"Naïve cynicism","description":"Expecting more egocentric bias in others than in oneself."},{"id":125,"title":"Naïve realism","description":"The belief that we see reality as it really is – objectively and without bias; that the facts are plain for all to see; that rational people will agree with us; and that those who don't are either uninformed, lazy, irrational, or biased."},{"id":126,"title":"Outgroup homogeneity bias","description":"Individuals see members of their own group as being relatively more varied than members of other groups."},{"id":127,"title":"Self-serving bias","description":"The tendency to claim more responsibility for successes than failures. It may also manifest itself as a tendency for people to evaluate ambiguous information in a way beneficial to their interests (see also group-serving bias)."},{"id":128,"title":"Shared information bias","description":"Known as the tendency for group members to spend more time and energy discussing information that all members are already familiar with (i.e., shared information), and less time and energy discussing information that only some members are aware of (i.e., unshared information)."},{"id":129,"title":"Sociability bias of language","description":"The disproportionally higher representation of words related to social interactions, in comparison to words related to physical or mental aspects of behavior, in most languages. This bias attributed to nature of language as a tool facilitating human interactions. When verbal descriptors of human behavior are used as a source of information, sociability bias of such descriptors emerges in factor-analytic studies as a factor related to pro-social behavior (for example, of Extraversion factor in the Big Five personality traits."},{"id":130,"title":"System justification","description":"The tendency to defend and bolster the status quo. Existing social, economic, and political arrangements tend to be preferred, and alternatives disparaged, sometimes even at the expense of individual and collective self-interest. (See also status quo bias.)."},{"id":131,"title":"Trait ascription bias","description":"The tendency for people to view themselves as relatively variable in terms of personality, behavior, and mood while viewing others as much more predictable."},{"id":132,"title":"Ultimate attribution error","description":"Similar to the fundamental attribution error, in this error a person is likely to make an internal attribution to an entire group instead of the individuals within the group."},{"id":133,"title":"Worse-than-average effect","description":"A tendency to believe ourselves to be worse than others at tasks which are difficult."},{"id":134,"title":"Bizarreness effect","description":"Bizarre material is better remembered than common material."},{"id":135,"title":"Choice-supportive bias","description":"In a self-justifying manner retroactively ascribing one's choices to be more informed than they were when they were made."},{"id":136,"title":"Change bias","description":"After an investment of effort in producing change, remembering one's past performance as more difficult than it actually was."},{"id":137,"title":"Childhood amnesia","description":"The retention of few memories from before the age of four."},{"id":138,"title":"Conservatism or Regressive bias","description":"Tendency to remember high values and high likelihoods/probabilities/frequencies as lower than they actually were and low ones as higher than they actually were. Based on the evidence, memories are not extreme enough."},{"id":139,"title":"Consistency bias","description":"Incorrectly remembering one's past attitudes and behaviour as resembling present attitudes and behaviour."},{"id":140,"title":"Context effect","description":"That cognition and memory are dependent on context, such that out-of-context memories are more difficult to retrieve than in-context memories (e.g., recall time and accuracy for a work-related memory will be lower at home, and vice versa)."},{"id":141,"title":"Cross-race effect","description":"The tendency for people of one race to have difficulty identifying members of a race other than their own."},{"id":142,"title":"Cryptomnesia","description":"A form of misattribution where a memory is mistaken for imagination, because there is no subjective experience of it being a memory."},{"id":143,"title":"Egocentric bias","description":"Recalling the past in a self-serving manner, e.g., remembering one's exam grades as being better than they were, or remembering a caught fish as bigger than it really was."},{"id":144,"title":"Fading affect bias","description":"A bias in which the emotion associated with unpleasant memories fades more quickly than the emotion associated with positive events."},{"id":145,"title":"False memory","description":"A form of misattribution where imagination is mistaken for a memory."},{"id":146,"title":"Generation effect (Self-generation effect)","description":"That self-generated information is remembered best. For instance, people are better able to recall memories of statements that they have generated than similar statements generated by others."},{"id":147,"title":"Google effect","description":"The tendency to forget information that can be found readily online by using Internet search engines."},{"id":148,"title":"Hindsight bias","description":"The inclination to see past events as being more predictable than they actually were; also called the 'I-knew-it-all-along' effect."},{"id":149,"title":"Humor effect","description":"That humorous items are more easily remembered than non-humorous ones, which might be explained by the distinctiveness of humor, the increased cognitive processing time to understand the humor, or the emotional arousal caused by the humor."},{"id":150,"title":"Illusion of truth effect","description":"That people are more likely to identify as true statements those they have previously heard (even if they cannot consciously remember having heard them), regardless of the actual validity of the statement. In other words, a person is more likely to believe a familiar statement than an unfamiliar one."},{"id":151,"title":"Illusory correlation","description":"Inaccurately remembering a relationship between two events."},{"id":152,"title":"Lag effect","description":"The phenomenon whereby learning is greater when studying is spread out over time, as opposed to studying the same amount of time in a single session. See also spacing effect."},{"id":153,"title":"Leveling and sharpening","description":"Memory distortions introduced by the loss of details in a recollection over time, often concurrent with sharpening or selective recollection of certain details that take on exaggerated significance in relation to the details or aspects of the experience lost through leveling. Both biases may be reinforced over time, and by repeated recollection or re-telling of a memory."},{"id":154,"title":"Levels-of-processing effect","description":"That different methods of encoding information into memory have different levels of effectiveness."},{"id":155,"title":"List-length effect","description":"A smaller percentage of items are remembered in a longer list, but as the length of the list increases, the absolute number of items remembered increases as well. For example, consider a list of 30 items ('L30') and a list of 100 items ('L100'). An individual may remember 15 items from L30, or 50%, whereas the individual may remember 40 items from L100, or 40%. Although the percent of L30 items remembered (50%) is greater than the percent of L100 (40%), more L100 items (40) are remembered than L30 items (15)."},{"id":156,"title":"Misinformation effect","description":"Memory becoming less accurate because of interference from post-event information."},{"id":157,"title":"Modality effect","description":"That memory recall is higher for the last items of a list when the list items were received via speech than when they were received through writing."},{"id":158,"title":"Mood-congruent memory bias","description":"The improved recall of information congruent with one's current mood."},{"id":159,"title":"Next-in-line effect","description":"People taking turns speaking in a group tend to have diminished recall for the words of others who spoke immediately before them."},{"id":160,"title":"Part-list cueing effect","description":"That being shown some items from a list and later retrieving one item causes it to become harder to retrieve the other items."},{"id":161,"title":"Peak-end rule","description":"That people seem to perceive not the sum of an experience but the average of how it was at its peak (e.g., pleasant or unpleasant) and how it ended."},{"id":162,"title":"Persistence","description":"The unwanted recurrence of memories of a traumatic event."},{"id":163,"title":"Picture superiority effect","description":"The notion that concepts that are learned by viewing pictures are more easily and frequently recalled than are concepts that are learned by viewing their written word form counterparts."},{"id":164,"title":"Positivity effect (Socioemotional selectivity theory)","description":"That older adults favor positive over negative information in their memories."},{"id":165,"title":"Primacy effect, recency effect & serial position effect","description":"hat items near the end of a sequence are the easiest to recall, followed by the items at the beginning of a sequence; items in the middle are the least likely to be remembered."},{"id":166,"title":"Processing difficulty effect","description":"That information that takes longer to read and is thought about more (processed with more difficulty) is more easily remembered."},{"id":167,"title":"Reminiscence bump","description":"The recalling of more personal events from adolescence and early adulthood than personal events from other lifetime periods."},{"id":168,"title":"Rosy retrospection","description":"The remembering of the past as having been better than it really was."},{"id":169,"title":"Self-relevance effect","description":"That memories relating to the self are better recalled than similar information relating to others."},{"id":170,"title":"Source confusion","description":"Confusing episodic memories with other information, creating distorted memories."},{"id":171,"title":"Spacing effect","description":"That information is better recalled if exposure to it is repeated over a long span of time rather than a short one."},{"id":172,"title":"Spotlight effect","description":"The tendency to overestimate the amount that other people notice your appearance or behavior."},{"id":173,"title":"Stereotypical bias","description":"Memory distorted towards stereotypes (e.g., racial or gender), e.g., 'black-sounding' names being misremembered as names of criminals."},{"id":174,"title":"Suffix effect","description":"Diminishment of the recency effect because a sound item is appended to the list that the subject is not required to recall."},{"id":175,"title":"Suggestibility","description":"A form of misattribution where ideas suggested by a questioner are mistaken for memory."},{"id":176,"title":"Telescoping effect","description":"The tendency to displace recent events backward in time and remote events forward in time, so that recent events appear more remote, and remote events, more recent."},{"id":177,"title":"Testing effect","description":"The fact that you more easily remember information you have read by rewriting it instead of rereading it."},{"id":178,"title":"Tip of the tongue phenomenon","description":"When a subject is able to recall parts of an item, or related information, but is frustratingly unable to recall the whole item. This is thought to be an instance of 'blocking' where multiple similar memories are being recalled and interfere with each other."},{"id":179,"title":"Travis Syndrome","description":"Overestimating the significance of the present. It is related to the enlightenment Idea of Progress and chronological snobbery with possibly an appeal to novelty logical fallacy being part of the bias."},{"id":180,"title":"Verbatim effect","description":"That the 'gist' of what someone has said is better remembered than the verbatim wording. This is because memories are representations, not exact copies."},{"id":181,"title":"von Restorff effect","description":"That an item that sticks out is more likely to be remembered than other items."},{"id":182,"title":"Zeigarnik effect","description":"That uncompleted or interrupted tasks are remembered better than completed ones."}];

