import React from 'react';

const Card = ({ title, description, id }) => { 
    return (
        <div style={{width:'320px' }}className="tc grow bg-light-green w5 h7 dib br3 pa4 ma3 bw2 shadow-5">
            <img alt="biases" src={`https://robohash.org/${id}?size=200x200`} />
            <div>
                <h2>{title}</h2>
                <p>{description}</p>
            </div>
        </div>
    );
}

export default Card;

