import React, { Component } from 'react';
import { connect } from 'react-redux';
import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import { biaseslist } from '../biaseslist';
import Scroll from '../components/Scroll';
import ErrorBoundary from '../components/ErrorBoundary';
import './App.css';
import { setSearchField } from '../actions';


const mapStateToProps = state => {
    return {
        searchField: state.searchField
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onSearchChange: (event) => dispatch(setSearchField(event.target.value))
    }
}

class App extends Component {
    constructor() {
        super()
        this.state = {
            biaseslist: []
        }
    }

    componentDidMount() {
          this.setState( { biaseslist } );
    }

    onSearchChange = (event) => {
        this.setState({ searchfield: event.target.value })
    }

    render() {
        const { biaseslist } = this.state;
        const { searchField, onSearchChange } = this.props;
        const filteredBiases = biaseslist.filter(bias => {
        return bias.title.toLowerCase().includes(searchField.toLowerCase());
        })
        if (biaseslist.length === 0) {
            return <h1>Loading</h1>
        } else {
            return (
        
    
        <div className="tc">
            <h1 className="f1">Blindspots</h1>
            <SearchBox searchChange={onSearchChange}/>
            <Scroll>
                <ErrorBoundary>
                    <CardList biaseslist={filteredBiases}/>
                </ErrorBoundary>
            </Scroll>
        </div>
        );
    }
}
}
export default connect(mapStateToProps, mapDispatchToProps)(App);


